.banner {
    padding: 0;
    margin: 0;
    background: linear-gradient(-45deg, #ee7752, #c97a46, #53aecf, #23d5ab);
    background-size: 500% 500%;
    animation: gradient 5s ease infinite;
    height: 720px;
    
    color: #fff;
  }
  @keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
  
