.tooltip-container{
    position: relative;
    cursor: pointer;
}
.tooltip-text{
    margin-left: 10px;
    margin-right: 26px;
    font-size: 20px;
}
.tooltip-dialog {
    position: absolute;
    top: 100%;
    left: 200%;
    transform: translateX(-50%);
    background-color: white;
    color: black;
    padding: 15px;
    padding-left: 1rem;
    padding-right: 20rem;
    border-radius: 4px;
    display: none;
  }
  .tooltip-container:hover .tooltip-dialog {
    display: block;
  }  

